<template>
  <div class="info">
    <HeadTop :lang="lang" @changeAppLocale="changeAppLocale" :name="this.$t('message.m_2') " />



    <div class="cent">
      <div class="ming_bix">
        <van-row type="flex" justify="space-between" class="">
          <van-col span="24"><span>{{ $t("message.f_22") }}（USDT）</span>
            <h3>{{ hmdInfo ? hmdInfo.info[10] : "0" | figure }} <br /> <small>≈
                {{ hmdInfo ? ((Number(hmdInfo.info[10])) * Number(hmdInfo.fivePrice)) : "0" | figure }} Flare</small>
            </h3>
          </van-col>
        </van-row>
        <div class="cent_line"></div>
        <van-row type="flex" justify="space-between" class="">
          <van-col span="24"><span>{{ $t("message.f_23") }}（USDT）</span>
            <h3>{{ hmdInfo ? hmdInfo.info[6] : "0" | figure }} <br /> <small>≈
                {{ hmdInfo ? ((Number(hmdInfo.info[6])) * Number(hmdInfo.fivePrice)) : "0" | figure }} Flare</small>
            </h3>
          </van-col>
        </van-row>

        <!-- <button class="btn_no" >
                {{ $t("message.f_28") }}
              </button> -->
      </div>

      <div class="bg">
        <button class="btn" @click="getReward">
          {{ $t("message.f_24") }}
        </button>
        <div class="ming">
          <div class="my">
            <van-row type="flex" justify="space-between" gutter="10">
              <van-col span="12">
                <div class="box">
                  <span>{{ $t("message.f_29") }}</span>
                  <h3>{{ hmdInfo ? hmdInfo.info[2] : "0" | figure }} USDT</h3>
                </div>

              </van-col>
              <van-col span="12">
                <div class="box">
                  <span>{{ $t("message.f_26") }}</span>
                  <h3>{{ hmdInfo ? (Number(hmdInfo.info[4])+Number(hmdInfo.info[5])+Number(hmdInfo.info[34])) : "0" |
                  figure
                  }} USDT </h3>
                  <!-- <br /> <small>≈{{ hmdInfo ? (
    (Number(hmdInfo.info[4])+Number(hmdInfo.info[5])+Number(hmdInfo.info[34])) * Number(hmdInfo.fivePrice))
    : "0" | figure
}} FAN</small> -->
                </div>

              </van-col>
              <van-col span="12">
                <div class="box">
                  <span>{{ $t("message.f_27") }}</span>
                  <h3>{{ hmdInfo ?
                  (Number(hmdInfo.info[1])*2-Number(hmdInfo.info[4])-Number(hmdInfo.info[5])-Number(hmdInfo.info[34]))
                  : "0" | figure }} USDT</h3>
                </div>

              </van-col>
              <van-col span="12">
                <div class="box">
                  <span>{{ $t("message.f_28") }}</span>
                  <h3>{{ hmdInfo ? (Number(hmdInfo.info[18])) : "0" | figure }} Flare</h3>
                </div>

              </van-col>
            </van-row>
          </div>

          <div class="list">
            <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"><span>{{ $t("message.f_25") }}</span></van-col>
              <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[1] : "0" | figure }} USDT</span>
              </van-col>
            </van-row>
            <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"><span>{{ $t("message.f_30") }}</span></van-col>
              <van-col span="12" class="textRight"><span> {{ hmdInfo ? live(hmdInfo.info[7]): "--" }}</span>
              </van-col>
            </van-row>
            <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"><span>{{ $t("message.f_31") }}</span></van-col>
              <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[12] : "0" }} </span>
              </van-col>
            </van-row>
            <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"><span>{{ $t("message.f_32") }}</span></van-col>
              <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[4] : "0" | figure }} USDT</span>
              </van-col>
            </van-row>
            <!-- <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"
                ><span>全球累计分红收益</span></van-col
              >
              <van-col span="12" class="textRight"
                ><span>{{ hmdInfo ? hmdInfo.info[34] : "0"  | figure }} USDT</span></van-col
              >
            </van-row> -->
            <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"><span>{{ $t("message.f_33") }}</span></van-col>
              <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[5] : "0" | figure }} USDT</span>
              </van-col>
            </van-row>
            <!-- <van-row type="flex" justify="space-between" class="line">
            <van-col span="12" class="textLeft"><span>{{ $t("message.f_34") }}</span></van-col>
            <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[34] : "0" | figure }} USDT</span>
            </van-col>
          </van-row> -->
            <!-- <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"><span>投资笔数</span></van-col>
              <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[13] : "0" }} </span></van-col>
            </van-row> -->
            <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"><span>{{ $t("message.f_35") }}</span></van-col>
              <van-col span="12" class="textRight"><span>{{ hmdInfo ? hmdInfo.info[17] : "0" | figure }} USDT</span>
              </van-col>
            </van-row>
            <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"><span>{{ $t("message.f_36") }}</span></van-col>
              <van-col span="12" class="textRight"><span>{{
              protectAddress('0x7169fA366ED2D053F34d24A8E0C73C1C9595Cf27',4) }}</span></van-col>
            </van-row>
            <van-row type="flex" justify="space-between" class="line">
              <van-col span="12" class="textLeft"><span>{{ $t("message.f_37") }}</span></van-col>
              <van-col span="12" class="textRight"><span>{{ link }}</span></van-col>
            </van-row>
          </div>
          <button class="btn" v-clipboard:copy="link" v-clipboard:success="onCopy" v-clipboard:error="onError">{{
          $t("message.f_21") }}
          </button>
        </div>
      </div>


    </div>
  </div>
</template>

<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
      lang: "",
      //   currency: "",
      link: "--",
      //   coinBalance: 0,
      //   isApprove: false,
      //   stakeAmount: 1,
      //   withdrawAmount: 0,
      timer: null,
      timer2: null,
      TokenPrice: 0,
      //   show: false,
    };
  },
  computed: {
    ...mapState("main", ["hmdInfo", "mxtContract", "account",]),
  },
  methods: {
    ...mapActions("main", ["add_hmdInfo", "add_mxtCoinContract"]),
    live(num) {
      switch (num) {
        case '0':
          return '--';
        case '4':
          return 'ID';
        case '3':
          return 'OG';
        case '2':
          return 'MD';
        case '1':
          return 'FANS';
          
      }
    },
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
    getUsdt(num) {
      return num * this.hmdInfo.fivePrice;
    },
    // 复制成功时的回调函数
    onCopy(e) {
      var txt = this.$t("message.copySuccee");
      this.$toast.success(txt);
    },
    // 复制失败时的回调函数
    onError(e) {
      var txt = this.$t("message.copyFail");
      this.$toast.fail(txt);
    },
    getUrl: function (addr) {
      var hash = window.location.hash;
      if (hash.indexOf("?") != -1) {
        hash = hash.split("?")[0];
      }
      console.log(hash);
      var url = window.location.origin + window.location.pathname + "#/home/";
      var newAddr = "";
      if (addr) {
        newAddr = addr.replace('"', "").replace('"', "");
        this.link = url + "?invite=" + newAddr;
      }
      return this.link;
    },
    protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(38, 42)
        : addr;
    },
    getPrice() {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      this.mxtContract.methods
        .getTokenPrice(
          "0x0496c8f51b6e6500bdd6e3bc0664857e976c6bc6",
          "0x6e42eae3317a19eb4b5115b0bce388bbf21cb917",
          "1000000000000000000"
        )
        .call()
        .then((res) => {
          // console.log('shdjsh')
          this.TokenPrice = parseFloat(
            web3.utils.fromWei(res, "ether")
          ).toFixed(4);
          console.log("getTokenPrice======", web3.utils.fromWei(res, "ether"));
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    getReward: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(account);
      this.mxtContract.methods
        .withdrawProfit()
        .send({ from: account, value: '5000000000000000' })
        .then((res) => {
          console.log("withdrawProfit", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_hmdInfo();
          //   _this.getCoinBalance();
          //   _this.add_hmdCoinContract();
          // }
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },



  },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    this.currency = "HMD";
    if (this.$route.query.r) {
      localStorage.setItem("r", this.$route.query.r);
    }
    var getData = () => {
      this.add_hmdInfo();
      this.add_mxtCoinContract();
      // this.add_aesCoinContract();
    };
    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.hmdInfo || !this.mxtContract) {
          getData();
        } else {
          _this.dataInfo = _this.hmdInfo.info;
          // _this.getPrice();
          if (_this.hmdInfo.inviteCode || _this.hmdInfo.referrer) {
            var invite = _this.hmdInfo.inviteCode || _this.hmdInfo.referrer;
            _this.getUrl(invite);
          }
          clearInterval(this.timer);
        }
      }, 1000);
    };

    getDataCalculate();
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 10000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
  components: {
    HeadTop,
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang="less">
.info {
  background: url(../../assets/img/fan/bg2.png) top center no-repeat;
  background-size: 100%;
  padding-top: 60px;

  .top {
    text-align: center;
    padding: 30px;
    width: 370px;
    height: 188px;
    box-sizing: border-box;
    // background: url(../../assets/img/info/top_bg.png) top center no-repeat;
    // background-size: 100%;
    margin: 5px auto 20px;
    box-sizing: border-box;

    .sl {
      margin-bottom: 10px;
    }

    span {
      color: #eee;
      font-family: PingFangSC-Regular, PingFang SC;
      font-size: 12px;
    }

    h2 {
      font-size: 18px;
      margin-top: 4px;
      margin-bottom: 12px;
      font-family: DINAlternate-Bold, DINAlternate;
      font-weight: bold;
      color: #ffffff;
    }
  }

  .title {
    font-size: 26px;
    font-family: SourceHanSansCN-Bold, SourceHanSansCN;
    font-weight: bold;
    color: #4da9db;
    line-height: 39px;
    margin: 0 0 30px;

    span {
      color: #ffa0ae !important;
      font-size: 26px !important;
      font-weight: bold !important;
    }
  }

  .cent {
    // background: url(../../assets/img/info/bg.png) top center no-repeat,
    //   url(../../assets/img/3.png) bottom center no-repeat;
    // background-size: 100%, 100%;
    padding: 10px 0 50px;

    .ming {
      width: 92%;
      margin: 30px auto;
      padding: 110px 0 50px;
      position: relative;
      background: #171111;
      border-radius: 15px;
      background: url(../../assets/img/fan/info_bg.png) top center no-repeat;
      background-size: 100%;


      .list {
        background: rgba(32, 41, 92, 1);
        // border-radius: 0 0 15px 15px;
        padding: 20px;
        width: 90%;
        margin: 20px auto 20px;
        box-sizing: border-box;

        .line {
          background: rgba(12, 13, 33, 1);
          // border-radius: 10px;
          margin: 10px 0;
          padding: 5px 10px;
        }

        span {
          display: inline-block;
          padding: 6px 0;
          color: #fff;
        }

        .textRight span {
          font-size: 15px;
          font-family: PingFangSC-Medium, PingFang SC;
          font-weight: 600;
          color: #26DDFE;
          overflow: hidden;
          display: block;
          width: 100%;
          text-align: right;
          text-overflow: ellipsis;
        }

      }
    }

    .my {
      margin: 0 auto 20px;
      padding: 0 10px;

      .box {
        background: rgba(32, 41, 92, .7);
        border-radius: 10px;
        margin: 5px 0;
        padding: 15px 10px;
        text-align: center;

        span {
          color: #fff;

          img {
            margin-right: 5px;
            vertical-align: middle;
          }
        }

        h3 {
          text-align: center;
          color: #FFC38B;
          margin: 8px 0 0 0;
        }
      }
    }

    .ming_bix {
      // background: url(../../assets/img/fan/info_bg.png) top center no-repeat;
      // background-size: 100% 100%;
      width: 95%;
      margin: 0 auto;
      padding: 0px 40px 0px;
      box-sizing: border-box;

      .cent_line {
        height: 1px;
        border-bottom: 2px dashed rgb(160, 158, 158);
      }

      span {
        color: #FFFFFF;
        display: block;
        margin-top: 25px;
      }

      h3 {
        font-family: DINPro;
        font-weight: bold;
        color: #26DDFE;
        font-size: 26px;
        line-height: 24px;
        margin-top: 10px;
        margin-bottom: 25px;

        small {
          color: #fff;
          font-size: 15px;
        }
      }

    }
  }
}

.btn {
  width: 60%;
  margin: 0 auto 20px;
  display: block;
  background: #00A9C1;
  border: none;
  text-align: center;
  color: #fff;
  font-family: PingFangSC-Medium, PingFang SC;
  font-weight: 500;
  font-size: 17px;
  border-radius: 10px;
  padding: 15px 0;
}

.bg {
  background: url(../../assets/img/fan/info_bg2.png) top center no-repeat;
  background-size: 100%;
}
</style>



