<template>
  <div class="info">
    <HeadTop :lang="lang" @changeAppLocale="changeAppLocale" :page1="'bonus'" :name="'升级'" />
         <div class="updata">
        <h1>每日排单总额度</h1>
         <p >*默认30000U,输入100及整倍数，改变每日排单总额度。</p>
        <input type="number" placeholder="0" v-model="usdt1">
         <button class="btn" @click="changeInvestUsdt(1)">确认更改</button>
      </div>
          <div class="updata">
        <h1>剩余排单额度</h1>
        <p >*默认20000U,输入100及整倍数，改变剩余排单额度。</p>
        <input type="number" placeholder="0" v-model="usdt2">
         <button class="btn" @click="changeInvestUsdt(2)">确认更改</button>
      </div>
        <!-- <div class="updata">
        <h1>最大投资限制</h1>
         <p >*默认1000U,输入100及整倍数，改变V1升级标准。</p>
        <input type="number" placeholder="0" v-model="usdt3">
         <button class="btn" @click="changeInvestUsdt(3)">确认更改</button>
      </div> -->
       <div class="updata">
        <h1>更改分红比例</h1>
        <p >*默认比例为4%，输入正整数即可修改，例如输入30确定修改，则静态分红比例为：3%</p>
        <input type="number" placeholder="0" v-model="rate">
         <button class="btn" @click="changeDividendRate(3)">确认更改</button>
      </div>
       <!-- <div class="updata">
        <h1>是否开启NFT持有者投资</h1>
        <p >*输入0则开启，1则是关闭</p>

        <input type="number" placeholder="0" v-model="open">
         <button class="btn" @click="changeConfig(1)">确认更改</button>
      </div> -->
       <!-- <div class="updata">
        <h1>账户只允许投资一次</h1>
        <p >*输入0则开启，1则是关闭</p>
        <input type="number" placeholder="0" v-model="money">
         <button class="btn" @click="changeConfig(3)">确认更改</button>
      </div> -->
       <!-- <div class="updata">
        <h1>每个号限制最大可投额度</h1>
          <p >*输入0则开启，1则是关闭</p>
        <input type="number" placeholder="0" v-model="max">
         <button class="btn" @click="changeConfig(2)">确认更改</button>
      </div> -->
            <div class="updata">
        <h1>升级V1额度</h1>
         <p >*默认1000U,输入100及整倍数，改变V1升级标准。</p>
        <input type="number" placeholder="0" v-model="val1">
         <button class="btn" @click="changeLevelLimitFun(1)">确认升级</button>
      </div>
       <div class="updata">
        <h1>升级V2额度</h1>
         <p >*默认2000U,输入100及整倍数，改变V2升级标准。</p>
        <input type="number" placeholder="0"  v-model="val2">
         <button class="btn" @click="changeLevelLimitFun(2)">确认升级</button>
      </div>
       <div class="updata">
        <h1>升级V3额度</h1>
         <p >*默认3000U,输入100及整倍数，改变V3升级标准。</p>
        <input type="number" placeholder="0" v-model="val3">
         <button class="btn" @click="changeLevelLimitFun(3)">确认升级</button>
      </div>
       <div class="updata">
        <h1>升级V4额度</h1>
         <p >*默认4000U,输入100及整倍数，改变V4升级标准。</p>
        <input type="number" placeholder="0" v-model="val4">
         <button class="btn" @click="changeLevelLimitFun(4)">确认升级</button>
      </div>
       <div class="updata">
        <h1>升级V5额度</h1>
         <p >*默认5000U,输入100及整倍数，改变V5升级标准。</p>
        <input type="number" placeholder="0" v-model="val5">
         <button class="btn" @click="changeLevelLimitFun(5)">确认升级</button>
      </div>
  </div>
</template>

<script type="es6">
import HeadTop from "../../components/Header";
import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
      lang: "",
      val1:null,
      val2:null,
      val3:null,
      val4:null,
      val5:null,
      usdt1:null,
      usdt2:null,
      usdt3:null,
      open:null,
      rate:null,
      money:null,
      max:null,
    };
  },
  computed: {
    ...mapState("main", ["hmdInfo", "mxtContract", "account"]),
  },
  methods: {
    ...mapActions("main", ["add_hmdInfo"]),
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
      changeConfig: function (num) {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      if(num==1){
         var monye = _this.open;
      }else if(num==2){
         var monye = _this.max;
      }else if(num==3){
         var monye = _this.money;
      }
      if(monye == 0||!monye){
         this.$toast('请输入额度');
        return;
      }
      console.log(monye)
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtContract.methods
        .changeConfig(num,web3.utils.toWei(monye.toString()))
        .send({ from: account })
        .then((res) => {
          console.log("changeConfig", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_hmdInfo();
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
      changeInvestUsdt: function (num) {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      if(num==1){
         var monye = _this.usdt1;
      }else if(num==2){
         var monye = _this.usdt2;
      }else if(num==3){
         var monye = _this.usdt3;
      }
      if(monye == 0||!monye){
         this.$toast('请输入额度');
        return;
      }
      console.log(monye)
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtContract.methods
        .changeInvestUsdt(num,monye.toString())
        .send({ from: account })
        .then((res) => {
          console.log("changeInvestUsdt", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_hmdInfo();
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    //更改VIP等级达标参数
    changeLevelLimitFun: function (num) {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      if(num==1){
         var monye = _this.val1;
      }else if(num==2){
         var monye = _this.val2;
      }else if(num==3){
         var monye = _this.val3;
      }else if(num==4){
         var monye = _this.val4;
      }else if(num==5){
         var monye = _this.val5;
      }
      if(monye == 0||!monye){
         this.$toast('请输入额度');
        return;
      }
      console.log(monye)
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtContract.methods
        .changeLevelLimit(num,web3.utils.toWei(monye.toString()))
        .send({ from: account })
        .then((res) => {
          console.log("changeLevelLimitFun", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          _this.add_hmdInfo();
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
  },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "zhCHS");
    var getData = () => {
      this.add_hmdInfo();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.hmdInfo || !this.mxtContract) {
          getData();
        } else {
          // this.getMiningInfoFun(1);
          clearInterval(this.timer);
        }
      }, 1000);
    };

    getDataCalculate();
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 60000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
  components: {
    HeadTop,
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang="less">
.info {
padding-top:60px;
.updata{
    width: 80%;
    margin: 0 auto;
    padding: 20px 0;
    box-sizing: border-box;
    color:#fff;
  h1{
    font-size: 16px;
  }
  p{color: #999;margin-top: 5px;}
  input{
    border: 1px solid #b15819;
    background: #000;
     box-sizing: border-box;
    font-size: 22px;
    color:#fff;
    width: 100%;
    border-radius: 10px;
    padding:8px 10px;
    margin: 15px auto;
    display: block;
  }
  .btn{
    width: 50%;
    margin: 0 auto 20px;
    display: block;
     background: url(../../assets/img/fan/an.png)center no-repeat;
      background-size:100% 100%;
      border: none;
      text-align: center;
      color:#fff;
      font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 500;
    font-size: 17px;
    border-radius: 30px;
    padding:20px 0;
  }

}
}
</style>



